.growl__root {
    position        : fixed !important;
    display         : flex;
    flex-direction  : column;
    row-gap         : 10px;
    top             : 5px !important;
    right           : 5px !important;
    height          : auto !important;
    width           : 350px !important;
    background-color: transparent !important;
    z-index         : 9999999;
}

.Growl {
    padding            : 10px 10px 10px 20px;
    background-repeat  : no-repeat;
    background-position: 10px center;
    position           : relative;
    z-index            : 9999999;
    color              : #fff;
    background-color   : rgba(27, 24, 24, 0.8);
    border             : #000 0px solid;
    border-bottom-width: 5px;
    border-bottom-style: solid;
    overflow           : hidden;

    &.shadow {
        -moz-box-shadow   : 3px 5px 10px #888888 !important;
        -webkit-box-shadow: 3px 5px 10px #888888 !important;
        -khtml-box-shadow : 3px 5px 10px #888888 !important;
        -o-box-shadow     : 3px 5px 10px #888888 !important;
        box-shadow        : 3px 5px 10px #888888 !important;
    }

    &[data-type] {
        padding-left       : 50px;
        background-position: 8px 8px, 0 center;
        background-repeat  : no-repeat;
    }

    &[data-type="info"] {
        background-image   : url(data:image/png;base64,);
        border-bottom-color: rgb(52, 150, 231);
    }

    &[data-type="success"] {
        background-image   : url(data:image/png;base64,);
        border-bottom-color: rgb(46, 202, 25);
    }
    
    &[data-type="warning"] {
        background-image   : url(data:image/png;base64,);
        border-bottom-color: rgb(236, 180, 74);
    }
    
    &[data-type="error"] {
        background-image   : url(data:image/png;base64,);
        border-bottom-color: #D8000C;
    }

    .close {
        color      : #FFF !important;
        font-size  : 24px;
        font-weight: 400;
        position   : absolute;
        top        : 0px;
        right      : 5px;
        cursor     : pointer;
        display    : none;
        line-height: 1;
        text-shadow: 0 1px 0 #fff;
        opacity    : 0.5 !important;

        &:hover {
            opacity: 1 !important;
        }
    }

    .title {
        font-weight: bold;
        font-size  : 14px;
        width      : 280px;
        text-align : left;

        &:after {
            content: '\00a0:\00a0';
        }
    }

    &:hover {
        .close {
            display: block;
        }
    }
}